import { all, call, put, takeLatest } from 'redux-saga/effects';
import { loginUser as loginUserService } from '../services';
import { setAuthToken, setUserInfo } from '../actions';

// Login
function* loginUser({ payload }) {
  const { email, password, history } = payload;
  // console.log({ payload }, 'loginsaga payload');

  try {
    const result = yield call(loginUserService, email, password);
    console.log({ result }, 'loginsaga result');
    yield put(setUserInfo(result.body));
    yield put(setAuthToken(result.authToken));
    yield call(history.push, '/home');
  } catch (err) {
    console.log(err);
  }
}

// User Saga
export default function* root() {
  yield all([takeLatest('LOGIN_USER', loginUser)]);
}
